import React, { useEffect, useState } from "react";
import { aroutes, croutes } from "../../routes/sidebar";
import { NavLink, Route } from "react-router-dom";
import * as Icons from "../../icons";
import SidebarSubmenu from "./SidebarSubmenu";
import { Button } from "@windmill/react-ui";

// Imageimp
import thelogo from "../../assets/img/icarelogo.png";

function Icon({ icon, ...props }) {
  const Icon = Icons[icon];
  return <Icon {...props} />;
}

function SidebarContent() {
  const [role, setRole] = useState(1);
  // Sidebar roles protection
  useEffect(() => {
    const trole = localStorage.getItem("type");
    // console.log("Layout.js" + role);
    setRole(trole);
  }, []);
  return (
    <div className="py-4 text-gray-500 dark:text-gray-400">
      <div className="flex items-center justify-start">
        <img src={thelogo} className="w-8 h-8 ml-4 " />
        <a
          className="ml-2 text-lg font-bold text-green-700 dark:text-green-400"
          href="#"
        >
          <span className="text-red-700 dark:text-red-400">i</span>Care
        </a>
      </div>
      <ul className="mt-6">
        {role == 0
          ? aroutes.map((route) =>
              route.routes ? (
                <SidebarSubmenu route={route} key={route.name} />
              ) : (
                <li className="relative  px-6 py-3" key={route.name}>
                  <NavLink
                    exact
                    to={route.path}
                    className=" inline-flex items-center w-full text-sm font-semibold transition-colors duration-150 hover:text-gray-800 dark:hover:text-gray-200"
                    activeClassName="text-gray-800 dark:text-gray-100"
                  >
                    <Route path={route.path} exact={route.exact}>
                      <span
                        className="absolute inset-y-0 left-0 w-1 bg-purple-600 rounded-tr-lg rounded-br-lg"
                        aria-hidden="true"
                      ></span>
                    </Route>
                    <Icon
                      className="w-5 h-5"
                      aria-hidden="true"
                      icon={route.icon}
                    />
                    <span className="ml-4">{route.name}</span>
                  </NavLink>
                </li>
              )
            )
          : croutes.map((route) =>
              route.routes ? (
                <SidebarSubmenu route={route} key={route.name} />
              ) : (
                <li className="relative  px-6 py-3" key={route.name}>
                  <NavLink
                    exact
                    to={route.path}
                    className=" inline-flex items-center w-full text-sm font-semibold transition-colors duration-150 hover:text-gray-800 dark:hover:text-gray-200"
                    activeClassName="text-gray-800 dark:text-gray-100"
                  >
                    <Route path={route.path} exact={route.exact}>
                      <span
                        className="absolute inset-y-0 left-0 w-1 bg-purple-600 rounded-tr-lg rounded-br-lg"
                        aria-hidden="true"
                      ></span>
                    </Route>
                    <Icon
                      className="w-5 h-5"
                      aria-hidden="true"
                      icon={route.icon}
                    />
                    <span className="ml-4">{route.name}</span>
                  </NavLink>
                </li>
              )
            )}
      </ul>
      {/* <div className="px-6 my-6">
        <Button>
          Create account
          <span className="ml-2" aria-hidden="true">
            +
          </span>
        </Button>
      </div> */}
    </div>
  );
}

export default SidebarContent;
